<template>
	<view class="pages">
		<view class="hadersboxs">
			<view class="haderscontents">
				<view class="" @click="back">
					<image class="imgsss"
						src="data:image/png;base64,"
						mode=""></image>
				</view>
				<view class="">{{data.pointname}} -趋势图</view>
				<view class=""> </view>
			</view>
		</view>
		<view class="leave_cont">
			<view class="ul">
				<view class="li" @click="openDatePicker('stime')">
					<text>开始时间:</text>
					<view class="flex1">
						<view class="date">{{stime}}</view>
					</view>
				</view>
				<view class="li" @click="openDatePicker('etime')">
					<text>结束时间:</text>
					<view class="flex1">
						<view class="date">{{etime}}</view>
					</view>
				</view>
			</view>
			<view class="headerrights">
				<view class="rights-lefts" @click="query">
					<image class="leftsimgs"
						src="data:image/png;base64,"
						mode=""></image>
					<text>查询</text>
				</view>
				<view class="rights-rights" @click="goFrequency">
					<image class="rightsimgs"
						src="data:image/png;base64,"
						mode=""></image>
					<text>时频谱</text>
				</view>
			</view>
		</view>

		<view class="content">
			<view class="charts-box">
				<qiun-data-charts class="chartsed" type="line" :opts="opts" :chartData="chartData" :ontouch="true"
					:onzoom="true" :disableScroll="true" />
			</view>
		</view>
		<!-- 单选框 -->
		<view class="groupbox">
			<u-radio-group v-model="value" @change="radioGroupChange" size="20px">
				<u-radio label-size="15px" icon-size="5px" @change="radioChange(item.code,item.signal)"
					v-for="(item, index) in radioList" :key="index" :name="item.name" :title="item.title"
					:disabled="item.disabled" active-color="#1890FF" class="uradios">
					{{item.name}}

				</u-radio>
			</u-radio-group>
		</view>

		<u-picker mode="time" v-model="show" :params="params" @confirm="confirm"></u-picker>
	</view>
</template>

<script>
	import {
		gatHeritems,
		gatcurveDatalist
	} from "@/api/api.js"
	export default {
		data() {

			return {
				dataIndex: [],
				dataValue: [],
				unitsed: '',
				signals: '',
				datayAxis: [],
				dataxAxis: [],
				params: {
					year: true,
					month: true,
					day: true,
					hour: true,
					minute: true,
					second: false
				},
				show: false, //时间选择器显示隐藏
				radioList: [

				],
				// u-radio-group的v-model绑定的值如果设置为某个radio的name，就会被默认选中
				value: '',
				codeed: '',
				stime: "",
				etime: "",
				currentTime: '',
				chartData: {},
				opts: {
					rotate: false,
					rotateLock: false,
					enableScroll: true,
					enableMarkLine: true,
					color: ["#1890FF"],
					dataPointShape: false,
					dataLabel: false,
					// padding: [30, 60, 0, 60],
					padding: [10, 60, 20, 68],
					legend: {
						show: false
					},
					xAxis: {
						splitNumber:2,
						gridEval:1,
						rotateLabel:true,
						rotateAngle:0.0001,
						fontSize:10,
						type: 'grid',
						gridType: 'dash',
						"labelCount": 5,
						disableGrid: true,
						boundaryGap: "justify",
						scrollShow: true,
						itemCount: 500000,
						scrollAlign: 'bottom', //滚动条初始位置
						scrollBackgroundColor: '#262b41', //默认为 #EFEBEF
						scrollColor: '#262b41', //默认为 #A6A6A6
						dashLength: 4,
							scrollAlign:'right',
					},
					yAxis: {
						fontSize: 12,
						gridType: "dash",
						dashLength: 1,
						tofix: 2
					},
					extra: {
						line: {
							type: "straight",
							width: 1
						}
					}
				},
			};
		},
		async onLoad(options) {

			// #ifdef APP-PLUS
			setTimeout(() => {
				plus.screen.lockOrientation("landscape-primary");
			}, 200);
			// #endif

			this.stime = this.change_date(3);
			this.etime = this.change_date(0);
			let data = JSON.parse(options.data);
			this.data = data
			this.pointname = this.data.pointname
			await this.getitemsList(this.data.pointcode);
			console.log(this.value, '单选')
			let currentRadio = this.radioList.find(item => item.name = this.value)
			let code = currentRadio.code
			this.signals = currentRadio.signal
			this.gatDatalist(code);

		},
		onShow() {
			setTimeout(() => {
				if (this.datayAxis == '') {
					uni.showLoading({
						title: '加载中'
					});
				}
			}, 800)

		},
		onUnload() {
			// #ifdef APP-PLUS
			plus.screen.lockOrientation('portrait-primary');
			// #endif

		},
		onReady() {
			this.getServerData();
		},
		methods: {
			change_date(days) {
				// 参数表示在当前日期下要增加的天数
				var now = new Date();
				// + 1 代表日期加，- 1代表日期减
				now.setDate((now.getDate() + 1) - 1 * days);
				var year = now.getFullYear();
				var month = now.getMonth() + 1;
				var day = now.getDate();

				var hour = now.getHours();
				var minute = now.getMinutes();
				var second = now.getSeconds();
				if (hour < 10)
					hour = '0' + hour;
				if (minute < 10)
					minute = '0' + minute;
				if (second < 10)
					second = '0' + second;
				if (month < 10) {
					month = '0' + month;
				}
				if (day < 10) {
					day = '0' + day;
				}
				return year + '-' + month + '-' + day + " " + hour + ":" + minute + ":" + second;
			},

			//点击查询
			query() {
				this.gatDatalist(this.pointId)
			},
			openDatePicker(time) {
				this.show = !this.show
				this.currentTime = time
			},
			confirm(time) {
				console.log(time)
				let {
					year: y,
					month: m,
					day: d,
					hour: h,
					minute: mm
				} = time
				// console.log(`${y}/${m}/${d} ${h}:${mm}`)
				this[this.currentTime] = `${y}/${m}/${d} ${h}:${mm}`
			},
			// 选中某个单选框时，由radio时触发
			radioChange(code, signal) {
				this.signals = signal
				let codes = this.gatDatalist(code)
				console.log(this.signals, 999)
			},
			// 选中任一radio时，由radio-group触发
			radioGroupChange(code) {

			},
			//单选框数据
			async getitemsList(pointcode) {
				let res = await gatHeritems(pointcode)
				console.log(res, "单选动态数据")
				if (res.statusCode == 200) {
					this.radioList = res.data
					let radioOption = {
						name: '温度',
						signal: 100,
						unit: '℃',
						// code: this.radioList[0].code,
						itemName: "temp"
					}
					this.radioList.push(radioOption)
					this.value = res.data[0].name
					console.log(radioOption.code, '数据1')

				}

			},

			//获取曲线数据
			gatDatalist(codes) {
				console.log(this.codes, 'signal')
				let params = {
					Origin: "app",
					RequestData: {
						// pointId:"10070010010301",
						pointId: codes,
						signalType: 0,
						startTime: this.stime,
						endTime: this.etime
					},
				}
				console.log(params.RequestData.pointId, "得到code")
				gatcurveDatalist(JSON.stringify(params)).then(res => {
					console.log(res, "10")
					if (res.statusCode == 200) {
						console.log(res, "10")
						this.datayAxis = res.data.data.map(item => {
							return (item.rms).toFixed(3)
						})
						this.dataxAxis = res.data.data.map(item => {
							return item.dtime
						})
						this.pointId = res.data.pointId
						this.unitsed = res.data.engineerUnit

						// 获取指定的数据
						this.dataIndex = res.data.data[0];
						this.dataValue = res.data.data[1];

						console.log(this.dataIndex, "codsss")
						// console.log(this.datayAxis)
						this.getServerData();

					}
				})
			},

			//ucharts-曲线图
			getServerData() {
				setTimeout(() => {
					let res = {
						categories: this.dataxAxis, //x轴
						series: [{ //y轴
							name: '单位:' + (this.unitsed) + "   " + "值",
							data: this.datayAxis
						}, ]
					};
					this.chartData = JSON.parse(JSON.stringify(res));
					// console.log(this.datayAxis,5425)
				}, 500);
			},


			//跳时频谱
			goFrequency() {
				let data = {
					pointname: this.pointname,
					pointId: this.pointId,
					signal: this.signals,
					dataIndex: this.dataIndex,
					dataValue: this.dataValue
				}
				console.log(data.pointId, "111")
				if (data.signal == 100) {
					uni.showModal({
						title: "请选择振动趋势",

					})
					return
				}
				if (data.pointId == '') {
					uni.showModal({
						title: "请勾选数据类型"
					})
				}
				if (data.pointId) {
					uni.navigateTo({
						url: `/pages/dgfrequency/index?data=${JSON.stringify(data)}`
					})
				}

			},

			back() {
				uni.navigateBack({
					delta: 1
				})
			},




		}
	};
</script>

<style lang="scss" scoped>
	.charts-box {
		color: #AFDDFF;
		width: 100%;
		height: 215px;
	}

	.chartsed {
		width: 100%;

	}

	.groupbox {
		display: flex;
		align-items: center;
		justify-content: space-around;
	}

	.uradios {
		color: #007AFF;
	}

	.pages-hareder {
		width: 99%;
		height: 50px;
		background-color: #007AFF;
	}


	@import "index.scss";
</style>
